
@import url("https://fonts.googleapis.com/css?family=Raleway:300,400,700");
@font-face {
  font-family: 'BebasNeue';
  src: url("./assets/fonts/348C6F_0_0.eot");
  src: url("./assets/fonts/348C6F_0_0.eot?#iefix") format("embedded-opentype"), url("./assets/fonts/348C6F_0_0.woff2") format("woff2"), url("./assets/fonts/348C6F_0_0.woff") format("woff"), url("./assets/fonts/348C6F_0_0.ttf") format("truetype");
}
.list-enter-active, .list-leave-active {
  -webkit-transition: all 1s;
  transition: all 1s;
}
.list-enter, .list-leave-to {
  opacity: 0;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.cv-container__loader {
  position: relative;
  height: 100px;
  width: 100%;
}
.v-spinner {
  width: 20%;
  top: 50%;
  left: 50%;
  text-align: center;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.cv {
  margin-top: 20px;
  margin-bottom: 20px;
}
.cv .cv-block-text {
    font-size: 1.6rem;
}
.cv-section__block--left .cv-section__block-title {
    text-align: right;
}
.cv-section__block--right .cv-section__block-title {
    text-align: left;
}
.cv-section__block-title {
    font-family: "BebasNeue", sans-serif;
    color: #8D8D8E;
    border-bottom: 1px dashed #AEAEAE;
    padding-bottom: 1rem;
}



