
.cv-section__work-experience {
  text-align: left;
}
.cv-section__work-experience .cv-block__wrapper {
    font-family: "Raleway", sans-serif;
    color: #333333;
}
.cv-section__work-experience .cv-block__wrapper-projects {
    padding-left: .5rem;
    font-size: 1.3rem;
}
.cv-section__work-experience .cv-block-company {
    font-weight: 700;
}
.cv-section__work-experience .cv-block-company .duration {
      font-weight: 400;
      color: #AEAEAE;
}
.cv-section__work-experience .cv-block-project-title,
  .cv-section__work-experience .cv-block-project-desc,
  .cv-section__work-experience .cv-block-project-url {
    font-size: 1.5rem;
    color: #898989;
    font-weight: 300;
}
.cv-section__work-experience .cv-block-project-title,
  .cv-section__work-experience .cv-block-project-desc {
    margin-top: 0;
    margin-bottom: 0;
}
.cv-section__work-experience .cv-block-project-url {
    margin-top: 0;
}



