
.cv-section__personal-main-photo {
  text-align: right;
  margin-bottom: 1rem;
}
.cv-section__personal-main-photo img {
    width: 60%;
    display: inline;
}
.cv-section__personal-main {
  text-align: right;
  font-family: "Raleway", sans-serif;
}
.cv-section__personal-main .cv-block-name {
    font-weight: 700;
    font-size: 10rem;
    line-height: .7;
    margin-top: 0;
    margin-bottom: 0;
}
.cv-section__personal-main .cv-block-position {
    font-weight: 300;
    font-size: 6rem;
    margin-top: 0;
    margin-bottom: 0;
}
.cv-section__personal-main .cv-block-phone,
  .cv-section__personal-main .cv-block-address,
  .cv-section__personal-main .cv-block-email,
  .cv-section__personal-main .cv-block-linkedin,
  .cv-section__personal-main .cv-block-github {
    font-weight: 300;
    font-size: 1.7rem;
    margin-top: 0;
    margin-bottom: 0;
}
.cv-section__personal-detail {
  text-align: right;
}
.cv-section__personal-detail .cv-block__wrapper {
    font-family: "Raleway", sans-serif;
    color: #AEAEAE;
}
.cv-section__personal-detail .cv-block-placedatebirth,
  .cv-section__personal-detail .cv-block-gender,
  .cv-section__personal-detail .cv-block-language,
  .cv-section__personal-detail .cv-block-religion {
    font-weight: 300;
    line-height: .8;
}
.cv-section__personal-detail .cv-block-placedatebirth .value,
    .cv-section__personal-detail .cv-block-gender .value,
    .cv-section__personal-detail .cv-block-language .value,
    .cv-section__personal-detail .cv-block-religion .value {
      font-weight: 700;
      color: #000000;
}



