
.cv-section__publication-research {
  text-align: left;
  font-family: "Raleway", sans-serif;
}
.cv-section__publication-research .cv-block__wrapper {
    font-family: "Raleway", sans-serif;
    color: #333333;
}
.cv-section__publication-research .cv-block-text {
    font-weight: 300;
}
.cv-section__publication-research .cv-block-publication-type {
    color: #333333;
    font-weight: 400;
}
.cv-section__publication-research .cv-block-publication-url {
    color: #333333;
    font-weight: 400;
}
.cv-section__publication-research .cv-block-publication-type,
  .cv-section__publication-research .cv-block-publication-title,
  .cv-section__publication-research .cv-block-publication-description {
    margin-bottom: 0;
}
.cv-section__publication-research .cv-block-publication-title,
  .cv-section__publication-research .cv-block-publication-description,
  .cv-section__publication-research .cv-block-publication-url {
    margin-top: 0;
}
.cv-section__publication-research .cv-block-publication-title,
  .cv-section__publication-research .cv-block-publication-url {
    margin-top: .2rem;
}



